<template>
    <div>
        <!-- 没有数据的购物车 -->
       <div v-if="!HaveDatas" class="emptyCart" style="">
           <img style="width:100px;heigth:100px" src="../assets/empty.png" alt="">
            <van-divider :style="{ color: 'black', borderColor: '#dbe2e8', padding: '0 16px' }">购物车暂无商品！</van-divider>
            <van-row>
                <van-col offset="8" span="8">
                     <van-button plain round size="large" type="danger"  @click='initData'>去逛逛</van-button>
                </van-col>
            </van-row>
        </div>
    </div>
</template>
<script>
import Vue from 'vue';    
import { Divider } from 'vant';
import { Button } from 'vant';
import { Panel } from 'vant';
import { Toast } from 'vant';

import { getTodayMenuDetail } from '../server/api/index'
Vue.use(Panel);
Vue.use(Button);
Vue.use(Divider);
export default {
    data(){
        return {
            HaveDatas:false
        }
    },
    methods:{
         async  initData() {
             let ref =  await getTodayMenuDetail('/lk01');
             if (ref.success) {
                 this.menulistDetail = ref.data.big_recommend.list;
                 this.data = ref.data.big_recommend.list;
             }
         console.log(ref);
        }
    }
}
</script>
<style >
.emptyCart {
    min-height: 700px;
    display: flex;
    flex-direction:column;
    justify-content: center;
    background-color: #f7f4f8;
}
.emptyCart img {
    padding-top: 1rem;
    width: 50%;
    height: 6rem;
    margin: 0 auto;
}
</style>


